import Vue from 'vue'
import axios from "axios";

// const URL = "/dev/api";
const URL = "http://api.guoxuekong.com/index.php/base";
const getAllParams = {
  app_type: process.env.NODE_ENV === 'h5',// ioswebandroid, undefined, text,
  app_version: "1.0.0",// 1.0.0, , text,
  notify_id: 1231231,// id, , text,
  app_request_time: 100000,//  , , text,
  lang: "zh-cn",// zh-cnen, zh-cn, text,
  client_id: "by04esfH0glASt",// id, , text,
  service_version: "100",// 100, 100, text,
  sign: "",
  uid: 0,
  sid: "",
  buss_data: "",
  country_no: "86"
}

//  
//    -
// POST
const get_code_params = {
  ...getAllParams,
  service_type: "by_SecurityCode_createAndSend",// , by_UserLoginSession_loginByMobileCode, text,
  accepter: "",// , , text,
  code_type: "1",// : 1:2,:3:4;:5;:6;:7;, 5, text,country_no:"",// , 86, text,
  code_length: 6
}

export const get_code = (data) => {
  return axios({
    method: "post",
    url: URL,
    data: { ...get_code_params,...data }
  })
}

